<template>
  <div class="home_wrap">
    <swiper-list :listImg="listImg"></swiper-list>
    <div class="home_content">
      <img-list :imgSrc="item.imgSrc" v-for="(item,index) in homeList" :key="index" :imgText="item.imgText"
                style="margin-top: 50px"></img-list>
    </div>
  </div>

</template>
<script>
  import imgList from '../components/imgList.vue'
  import swiperList from '../components/swiper.vue'

  export default {
    components: {
      imgList,
      swiperList
    },
    data () {
      return {
        listImg: [{
          url: require('../assets/swiper.jpeg'),
          swiper_text: ''
        }, {
          url: require('../assets/home_banner2.jpeg'), swiper_text: ''
        }, {
          url: require('../assets/home_banner3.jpeg'),
          swiper_text: ''
        }],
        homeList: [{
          imgSrc: require('../assets/shitu_w01.jpg'),
          imgText: ''
        },
          {imgSrc: require('../assets/sshitu_w02.jpg'), imgText: '?'},
          {imgSrc: require('../assets/shitu_w03.jpg'), imgText: ''},
          {imgSrc: require('../assets/shitu_w04.jpg'), imgText: ''},
          {imgSrc: require('../assets/shitu_w05.jpg'), imgText: ''},
          {imgSrc: require('../assets/shitu_w06.jpg'), imgText: ''},
          {imgSrc: require('../assets/shitu_w07.jpg'), imgText: ''},
          {imgSrc: require('../assets/shitu_w08.jpg'), imgText: ''},
          {imgSrc: require('../assets/shitu_w09.jpg'), imgText: ''},
          {imgSrc: require('../assets/shitu01.jpg'), imgText: ''},
          {imgSrc: require('../assets/shitu_w11.jpg'), imgText: ''},
          {imgSrc: require('../assets/shitu_w12.jpg'), imgText: 'pigu'}
        ]
      }
    }
  }
</script>
<style lang="stylus">
  .home_wrap {
    width: 100%;
    & .home_content {
      width: 1280px;
      margin: 0px auto;
      min-width: 1280px;
      display: flex;
      justify-content: space-between;
      flex-wrap: wrap;
    }
  }
</style>



